"use client";

export default function Register() {
  async function submit(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault();
    const b = Object.fromEntries(new FormData(e.target as HTMLFormElement).entries());
    const r = await fetch("/api/register", {
      method: "POST",
      headers: { "content-type": "application/json" },
      body: JSON.stringify(b)
    });
    const json = await r.json();
    alert(json.ok ? "Registered!" : "Error: " + (json.error || "unknown"));
  }

  return (
    <main className="mx-auto max-w-md p-6 prose">
      <h1>Register</h1>
      <form onSubmit={submit} className="space-y-3">
        <input
          name="email"
          type="email"
          required
          placeholder="you@example.com"
          className="w-full border rounded p-2"
        />
        <input
          name="password"
          type="password"
          required
          placeholder="password"
          className="w-full border rounded p-2"
        />
        <button className="mt-3 bg-black text-white px-3 py-1 rounded">
          Register
        </button>
      </form>
    </main>
  );
}
